<template>
  <a-layout id="components-layout-demo-custom-trigger">
    <a-layout-sider v-model="collapsed" :trigger="null" collapsible>
      <div class="logo"/>
      <div style="margin-top: 10%;"></div>
      <a-menu theme="dark" mode="inline" :default-selected-keys="['1']" @click="handleClick">
        <a-menu-item key="1">
          <a-badge :count="1" :offset="!collapsed?[20,0]:[8,8]" :dot="collapsed">
            <a-icon type="form"/>
            <span>咨询处理</span>
          </a-badge>

        </a-menu-item>
        <a-menu-item key="2">
          <a-icon type="solution"/>
          <span>高校管辖</span>
        </a-menu-item>
        <a-menu-item key="3">
          <a-icon type="user"/>
          <span>账号信息</span>
        </a-menu-item>
      </a-menu>
    </a-layout-sider>
    <a-layout>
      <a-layout-header style="background: #fff; padding: 0">
        <a-icon
            class="trigger"
            :type="collapsed ? 'menu-unfold' : 'menu-fold'"
            @click="() => (collapsed = !collapsed)"
            style="padding-left: 20px"
        />
        <a-tooltip>
          <template slot="title">
            刷新页面
          </template>
          <a-icon class="trigger" type="reload" @click=""/>
        </a-tooltip>

        <div class="user-icon">
          <a-dropdown>
            <a class="ant-dropdown-link" @click="e => e.preventDefault()">
              <a-avatar icon="user"/>
              <span>&nbsp; Username </span>
              <a-icon type="down"/>
            </a>
            <a-menu slot="overlay">
              <a-menu-item key="1">
                <a-icon type="user"/>
                账号信息
              </a-menu-item>
              <a-menu-item key="2">
                <a-icon type="logout"/>
                退出登录
              </a-menu-item>
            </a-menu>
          </a-dropdown>
        </div>

      </a-layout-header>
      <a-layout-content
          :style="{ margin: '24px 16px', padding: '24px', background: '#fff', minHeight: '280px' }"
      >
        <router-view></router-view>
      </a-layout-content>
      <a-layout-footer style="textAlign: center">
        Ant Design ©2022 Created by Ant UED
      </a-layout-footer>
    </a-layout>
  </a-layout>
</template>

<script>
  export default {
    name: 'Dashboard',
    data() {
      return {
        collapsed: false,
      };
    },
    methods: {
      handleClick(e) {
        // console.log(e.key)
        const key = e.key;
        switch (key) {
          case '1':
            this.$router.push({name: 'consults'});
            break;
          case '2':
            this.$router.push({name: 'university'});
            break;
          case '3':
            this.$router.push({name: 'userInfo'});
            break;
          default:
            this.$message.error('无效的点击！');
        }
      }
    }
  }
</script>

<style scoped>
  #components-layout-demo-custom-trigger {
    min-height: 721px;
  }

  #components-layout-demo-custom-trigger .trigger {
    font-size: 18px;
    line-height: 64px;
    padding: 0 10px;
    cursor: pointer;
    transition: color 0.3s;
  }

  #components-layout-demo-custom-trigger .trigger:hover {
    color: #1890ff;
  }

  #components-layout-demo-custom-trigger .logo {
    height: 32px;
    margin-top: 5%;
    margin-left: 5%;
  }

  .user-icon {
    display: inline-block;
    margin-left: 80%;
    vertical-align: middle;
  }

  .user-icon a {
    color: rgba(0, 0, 0, 0.70);
  }
</style>